.post-header,
.post-comments-body,
.public-post-follow {
    @include flex-item(initial, space-between, initial);
    padding: 15px;
}
.post-type-icon::before {
    content: '\00B7';
    padding: 8px 5px 0 0;
}
.post-date {
    display: inline-block;
    font-size: 12px;
    padding: 3px 0;

}
.publisher img {
    @include publisher-pic(45px,45px);
    border: $border;
}
.publisher-info {
    @include flex-item(column, initial, initial);
    padding: 6px 10px;
}

.light-padded {
    padding: 10px;
}

.follow-btn {
    @include button(5px 13px);
    border: $border;
    span {
        font-weight: $headers-font-weight;
        font-size: $headers-font-size;
    }
}

.post-preview {
    border: $border;
    width: 95%;
    margin: auto;
    box-shadow: 0px 1px 2px -2px rgba(0,0,0,0.75);
    img {
        max-width:100%;
        max-height:100%;
    }
    video {
        max-width:100%;
        max-height:100%;
    }
}

.post-source { 
    direction: initial;
    display: inherit;
}
.post-likes .fa-thumbs-up {
    color: #4679EB;
}
.post-comments-bottom {
    @include flex-item(initial, space-around, center);
    position: relative;
    padding: 12px;
    margin: auto 10px;
    border-top: $border;
}
.download {
    position: absolute;
    top: 11px;
    left: 0;
    img {
        height: 22px; width: 22px;
    }
}
.private-post .post-title {
    color: black;
    font-size: 25px;
}

.post-reactions {
    border-top: $border;
    justify-content: space-between;
    & img {
        width: 22px;
        height: 22px;
        margin-right: -4px;
    }
    & .reactions-num {
        margin-left: 10px; 
    }
    & .post-reactions-comments {
        @include flex-item(initial, space-between, center);
    }
}

.publisher-name {
    @include publisher-name();
}